import React, { useState } from 'react';
import { migrateDataToFirestore } from '../migrateData';

const MigrationRunner = () => {
  const [isRunning, setIsRunning] = useState(false);
  const [isComplete, setIsComplete] = useState(false);

  const runMigration = async () => {
    setIsRunning(true);
    try {
      await migrateDataToFirestore();
      setIsComplete(true);
    } catch (error) {
      console.error('Migration failed:', error);
    } finally {
      setIsRunning(false);
    }
  };

  if (isComplete) {
    return (
      <div className="bg-green-50 border border-green-200 rounded-lg p-4 mb-6">
        <h3 className="text-green-800 font-semibold">✅ Migration Complete!</h3>
        <p className="text-green-700 text-sm">Data has been successfully migrated to Firestore.</p>
      </div>
    );
  }

  return (
    <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-6">
      <h3 className="text-yellow-800 font-semibold">🚀 Data Migration Required</h3>
      <p className="text-yellow-700 text-sm mb-3">
        Click below to migrate sample data to Firestore. This only needs to be done once.
      </p>
      <button
        onClick={runMigration}
        disabled={isRunning}
        className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 disabled:opacity-50"
      >
        {isRunning ? 'Migrating...' : 'Run Migration'}
      </button>
    </div>
  );
};

export default MigrationRunner;
