export const migrateDataToFirestore = async () => {
  try {
    console.log('Starting data migration...');
    const batch = writeBatch(db);
    
    // Migrate modules
    SAMPLE_MODULES.forEach((module) => {
      const moduleRef = doc(collection(db, 'modules'), module.id);
      batch.set(moduleRef, module);
    });
    
    // Migrate cases
    SAMPLE_CASES.forEach((caseData) => {
      const caseRef = doc(collection(db, 'cases'), caseData.id);
      batch.set(caseRef, caseData);
    });
    
    await batch.commit();
    console.log('✅ Data migration completed successfully!');
    console.log(`Migrated ${SAMPLE_MODULES.length} modules and ${SAMPLE_CASES.length} cases`);
  } catch (error) {
    console.error('❌ Error migrating data:', error);
  }
};
